import React from 'react';
import googleIcon from '../../image/googleIcon.png';
import kakaoIcon from '../../image/kakaoIcon.svg';
import GoogleLogin from 'react-google-login';
import KakaoLogin from 'react-kakao-login';


const LoginWindow = ({setLoginWindow,responseGoogle,responseKakao})=>{
  const jsKey = "f803730b31a04aa1ffd90e1fbd5db921";
  // window.navigator.__defineGetter__('userAgent', function () {
  //   return 'Mozilla/5.0 (Linux; Android 4.1.1; Galaxy Nexus Build/JRO03C) AppleWebKit/535.19 (KHTML, like Gecko) Chrome/18.0.1025.166 Mobile Safari/535.19';
  // });
  if (!window.Kakao.isInitialized()) {
    // JavaScript key   SDK 
    window.Kakao.init(jsKey);
    // SDK   .
  }
  return(
    <div 
    className="modal_background"
    onClick={()=>{setLoginWindow(false)}}>
    <div className="modal_login"
      onClick={(e)=>{setLoginWindow(true);e.stopPropagation();}}>
          <div style={{height:'23px',lineHeight:"23px",fontSize:"32px",fontWeight:'bold',marginBottom:"24px",color:"#505050"}}></div>
          <div style={{lineHeight:"32.4px",textAlign:"center",marginBottom:"40px",fontSize:"14px",color:"#505050"}}>
                    .<br/>
                .
          </div>
          <div style={{position:"relative",width:"256px",height:"56px",
          display:/wv/i.test(navigator.userAgent)?"none":"flex",
          justifyContent:"center",alignItems:"center",marginBottom:"8px"}}>
              <GoogleLogin
                  clientId='148840721751-8otnv8h8pcuabmilq8mdv9ungtmohatc.apps.googleusercontent.com'
                  buttonText=""
                  onSuccess={responseGoogle}
                  onFailure={responseGoogle}
                  cookiePolicy={'single_host_origin'}
              />
              <div id="login_btn" className="btn_google" 
                onClick={(e)=>{
                  if(e.target.id ==="login_btn"){
                    e.target.parentNode.childNodes[0].click();
                  }else{
                    e.target.parentNode.parentNode.childNodes[0].click();
                  }
                }}>
                <div id="login_btn_icon" style={{backgroundImage:`url(${googleIcon})`,width:"24px",height:"24px",marginRight:"40px",marginLeft:"17px"}}></div>
                <div id="login_btn_text"style={{width:"96px",height:"20px"}} > </div>
              </div>
          </div>
          <div style={{position:"relative",width:"256px",height:"56px",display:"flex",justifyContent:"center",alignItems:"center",marginBottom:"40px"}}>
              <KakaoLogin
                    token='23171cd8148017846b0514bae6409fc6'
                    buttonText=""
                    onSuccess={responseKakao}
                    onFail={responseKakao}
                    needProfile
                    onLogout={console.info}
                    style={{opacity:0}}
                  
              />
              <div id="login_btn" 
                className="btn_kakao" 
                onClick={(e)=>{
                  if(e.target.id ==="login_btn"){
                    e.target.parentNode.childNodes[0].click();
                  }else{
                    e.target.parentNode.parentNode.childNodes[0].click();
                  }
                }}
                >
                <div id="login_btn_icon" style={{backgroundImage:`url(${kakaoIcon})`,width:"24px",height:"21px",marginRight:"37px",marginLeft:"18px"}}></div>
                <div id="login_btn_text"style={{width:"120px",height:"20px"}} > </div>
              </div>
          </div>
          
          <div style={{textAlign:"center",fontSize:14,color:"#a0a0a0"}}>
             .
          </div>
    </div>
  </div>
  )
}

export default LoginWindow;